import React from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { langContext } from "./../../Context/lang";
import { useContext, useState } from "react";
import "./Navbar.css";

const Header = () => {
  const [show, setshow] = useState(true);
  const toggle = () => {
    setshow(!show);
  };
  /* eslint eqeqeq: 0 */
  const fav = useSelector((state) => state.fav.fav);
  const { lang, setLang } = useContext(langContext);
  return (
    <>
      <nav
        className="navbar navbar-expand-lg justify-content-between d-flex "
        style={lang == "EN" ? { direction: "ltr" } : { direction: "rtl" }}
      >
        <div className="container-fluid">
          <Link className="navbar-brand" to="/" style={{}}>
            Moviely
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            onClick={() => {
              toggle();
            }}
          >
            <div class="d-flex flex-column justify-content-around">
              <span className="navbar-toggler-icon"></span>
              <span
                className="navbar-toggler-icon"
                style={{ margin: "5px 0" }}
              ></span>
              <span className="navbar-toggler-icon"></span>
            </div>
          </button>
          <div
            className="collapse navbar-collapse justify-content-between"
            style={show == true ? { display: "block" } : { display: "none" }}
            id="navbarNav"
          >
            <ul className="navbar-nav text-center">
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  to="/"
                  onClick={(event) => {
                    event.target.classList.add("nav-link-active");
                  }}
                >
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  to="/About"
                  onClick={(event) => {
                    event.target.classList.add("nav-link-active");
                  }}
                >
                  About US
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  to="/Movies"
                  onClick={(event) => {
                    event.target.classList.add("nav-link-active");
                  }}
                >
                  Movies
                </Link>
              </li>

              <li className="nav-item">
                <Link
                  className="nav-link active"
                  to="/Favorites"
                  onClick={(event) => {
                    event.target.classList.add("nav-link-active");
                  }}
                >
                  Favorites
                  <span className="badge text-bg-secondary bg-dark ms-2">
                    <i
                      className="bi bi-star-fill pe-2 "
                      style={{ color: "#f3ac0c", fontSize: "15px" }}
                    ></i>
                    {fav.length}
                  </span>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link active"
                  to="/Contact"
                  onClick={(event) => {
                    event.target.classList.add("nav-link-active");
                  }}
                >
                  Contact
                </Link>
              </li>
            </ul>
            <ul className="navbar-nav d-flex flex-row justify-content-center">
              <li>
                <button
                  class="btn btn-warning ms-3"
                  onClick={() => {
                    if (lang == "EN") {
                      setLang("AR");
                    } else {
                      setLang("EN");
                    }
                  }}
                >
                  {lang}
                </button>
              </li>
              <li>
                <Link className="btn btn-info me-2 ms-2" to="/Login">
                  Login
                </Link>
              </li>
              <li>
                <Link className="btn btn-warning" to="/Register">
                  Register
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};
export default Header;
