import React from "react";
import "./Footer.css";
import Link from "./../Reuseables/Links";

export default class Footer extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor() {
    super();
  }
  render() {
    return (
      <>
        <div className="footer">
          <div className="overlay"></div>
          <div className="container-fluid">
            <div className="row d-flex  justify-content-around">
              <div className="col-md-3">
                <div className="one">
                  <h3 className="mb-3">Information</h3>
                  <ul className="list-unstyled ">
                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link
                          className="link"
                          to="/"
                          name="Image Licenses"
                        ></Link>
                      </span>
                    </li>

                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="Contact US"></Link>
                      </span>
                    </li>
                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="Our Authors"></Link>
                      </span>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="col-md-3">
                <div className="two">
                  <h3 className="mb-3">Discove</h3>
                  <ul className="list-unstyled">
                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="All Reviews"></Link>
                      </span>
                    </li>
                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="Contact US"></Link>
                      </span>
                    </li>
                    <li className="footer-link">
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="Our Authors"></Link>
                      </span>
                    </li>
                  </ul>
                </div>
              </div>

              <div className="col-md-3 ">
                <div className="three text-start">
                  <h3 className="mb-3">Community</h3>
                  <ul className="list-unstyled text-start">
                    <li className="footer-link">
                      <i className="bi bi-facebook me-3"></i>
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="Facebook"></Link>
                      </span>
                    </li>
                    <li className="footer-link">
                      <i className="bi bi-whatsapp me-3"></i>
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="WhatsApp"></Link>
                      </span>
                    </li>
                    <li className="footer-link">
                      <i className="bi bi-linkedin me-3 "></i>
                      <span className="d-inline-block">
                        <Link className="link" to="/" name="LinkedIn"></Link>
                      </span>
                    </li>
                  </ul>
                </div>
              </div>
              <h4 className="mt-3 text-center">
                Made with
                <span role="img" aria-label="Snowman">
                  ❤️️
                </span>
                By Alaa Ezzat
              </h4>
            </div>
          </div>
        </div>
      </>
    );
  }
}
