import axiosInstance from "./../../axiosConfig/axiosInstance";
export default function changeMovies(page) {
  /* eslint eqeqeq: 0 */
  return (dispatch) => {
    axiosInstance
      .get(`discover/movie?page=${page}`)
      .then((res) => {
        dispatch({
          type: "SET_MOVIES",
          payload: res.data.results,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
}
