import React from "react";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import "./details.css";

function Details(props) {
  /* eslint eqeqeq: 0 */
  const movies = useSelector((state) => state.movies);
  const [movie, setMovie] = useState({});
  const params = useParams();

  useEffect(() => {
    movies.map((mov) => {
      if (mov.id == params.id) {
        setMovie(mov);
      }
      return true;
    });
  }, []);

  console.log(params.id);
  console.log(movie);
  return (
    <>
      <div className="DetailsCard pt-5">
        <div className="card col-12 m-auto p-5">
          <div className="row">
            <h2 className="head text-center">{movie.title} Details</h2>
            <div className="col-md-6">
              <div
                className="pic"
                style={{
                  backgroundImage: `url(https://image.tmdb.org/t/p/w500${movie.poster_path})`,
                }}
              ></div>
            </div>
            <div className="col-md-6">
              <div className="card-body">
                <div>
                  <div id="item-1">
                    <h5>Title</h5>
                    <p>{movie.title}</p>
                  </div>
                  <div id="item-2">
                    <h5>Overview</h5>
                    <p>{movie.overview}</p>
                  </div>
                  <div id="item-4">
                    <h5>Release Date </h5>
                    <p>{movie.release_date}</p>
                  </div>
                  <div id="item-5">
                    <h5>Rating </h5>
                    <p>{movie.vote_average}</p>
                  </div>
                  <div id="item-6">
                    <h5>Vote Count </h5>
                    <p>{movie.vote_count}</p>
                  </div>
                  <div id="item-7">
                    <h5>popularity</h5>
                    <p>{movie.popularity}</p>
                  </div>
                </div>

                <button className="btn btn-warning mt-3 w-50 ">Watch</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Details;
