import React from "react";
import { useSelector, useDispatch } from "react-redux";
import getFavMovies from "./../../store/actions/fav";
import { Link } from "react-router-dom";
import "./moviCard.css";

export default function MoviCard(props) {
  /* eslint eqeqeq: 0 */
  const fav = useSelector((state) => state.fav.fav);
  const dispatch = useDispatch();
  const addToFavorite = (event, movie) => {
    console.log(fav);
    if (fav.some((item) => item.id == movie.id)) {
      event.target.classList.remove("bi-star-fill");
      event.target.classList.add("bi-star");
      fav.map((Favmovie) => {
        if (Favmovie.id == movie.id) {
          var index = fav.indexOf(Favmovie);
          fav.splice(index, index + 1);
        }
        return true;
      });
    } else {
      event.target.classList.remove("bi-star");
      event.target.classList.add("bi-star-fill");
      fav.push(movie);
    }
    console.log(fav);
    dispatch(getFavMovies(fav));
  };
  return (
    <div className=" myCard col-xl-3  col-lg-4 col-sm-6 col-8">
      <div className="card">
        <span className="rate badge text-bg-secondary p-2">
          {props.movie.vote_average}
        </span>

        <span className="star badge text-bg-secondary p-2">
          <i
            className={props.is_fav ? "bi bi-star-fill" : "bi bi-star"}
            onClick={(event) => {
              addToFavorite(event, props.movie);
            }}
          ></i>
        </span>
        <div className="img-con">
          <div
            style={{
              backgroundImage: `url(https://image.tmdb.org/t/p/w500${props.movie.poster_path})`,
            }}
            className="card-img-top "
          />
        </div>
        <div className="card-body">
          <h5 className="card-title">
            {props.movie.title.length > 20
              ? props.movie.title.substr(0, 17) + "..."
              : props.movie.title}
          </h5>
          <Link to={`/Details/${props.movie.id}`} className="btn btn-general">
            Details
          </Link>
        </div>
      </div>
    </div>
  );
}
