import React from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import getFavMovies from "./../../store/actions/fav";
import "./favorite.css";

export default function Favorites() {
  const fav = useSelector((state) => state.fav.fav);
  const dispatch = useDispatch();

  let removeItem = (index) => {
    fav.splice(index, index + 1);
    dispatch(getFavMovies(fav));
  };

  return (
    <>
      <div className="Favorites">
        <div className="container pt-5 ">
          <div className="row justify-content-center">
            <h2 className="text-center mb-4 head">
              Good Choice, We hope you have a great time
            </h2>
            {fav.map((movie, index) => {
              return (
                <div key={index} className="col-md-3 myCard  ">
                  <div className="card">
                    <div className="delete badge text-bg-secondary p-2">
                      <i
                        className="bi bi-archive-fill"
                        onClick={(event) => {
                          removeItem(index);
                        }}
                      ></i>
                    </div>
                    <div className="img-con">
                      <div
                        style={{
                          backgroundImage: `url(https://image.tmdb.org/t/p/w500${movie.poster_path})`,
                        }}
                        className="card-img-top "
                      />
                    </div>
                    <div className="card-body">
                      <h5 className="card-title">{movie.title}</h5>
                      <Link
                        to={`/Details/${movie.id}`}
                        className="btn btn-general"
                      >
                        Details
                      </Link>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
}
