import { useState } from "react";
import React from "react";

const Register = () => {
  /* eslint eqeqeq: 0 */
  const [user, setUser] = useState({
    name: "",
    email: "",
    userName: "",
    password: "",
    repeatPassword: "",
  });

  const [errors, setErrors] = useState({
    nameErrors: null,
    emailErrors: null,
    userNameErrors: null,
    passwordErrors: null,
    repeatPasswordErrors: null,
  });

  const handleInputChange = (evt) => {
    /* eslint eqeqeq: 0 */
    if (evt.target.name == "name") {
      setUser({
        ...user,
        name: evt.target.value,
      });

      setErrors({
        ...errors,
        nameErrors:
          evt.target.value.length == 0 ? "this field is required" : "",
      });
    }
    // eslint-disable-next-line eqeqeq
    else if (evt.target.name == "email") {
      console.log("Email");
      setUser({
        ...user,
        email: evt.target.value,
      });
      let regex = new RegExp("[a-z0-9]+@[a-z]+[.]+[a-z]{2,3}");
      setErrors({
        ...errors,
        emailErrors:
          evt.target.value.length == 0
            ? "this field is required"
            : regex.test(evt.target.value)
            ? ""
            : "you should enter Valid Email",
      });
    } else if (evt.target.name == "userName") {
      setUser({
        ...user,
        userName: evt.target.value,
      });
      setErrors({
        ...errors,
        userNameErrors:
          evt.target.value.length == 0
            ? "this field is required"
            : evt.target.value.indexOf(" ") >= 0
            ? "User Name must not contain spaces "
            : "",
      });
    } else if (evt.target.name == "password") {
      console.log("password");
      setUser({
        ...user,
        password: evt.target.value,
      });

      /* const regex2 = new RegExp("^s*[0-9a-zA-Z]*$"); */
      const regex2 = new RegExp("^[A-Z0-9 _]*[A-Z0-9][A-Z0-9 _]*$");

      setErrors({
        ...errors,
        passwordErrors:
          evt.target.value.length == 0
            ? "this field is required"
            : evt.target.value.length < 8
            ? "the Password must be at least 8 character"
            : regex2.test(evt.target.value)
            ? "this field must contain  upperCased letters, numbers and lowerCased letters and speace"
            : "",
      });
    } else if (evt.target.name == "repeatPassword") {
      setUser({
        ...user,
        repeatPassword: evt.target.value,
      });

      setErrors({
        ...errors,
        repeatPasswordErrors:
          evt.target.value != user.password
            ? "the Repeated Password must Match password"
            : "",
      });
    }
  };

  const [passwordType, setPasswordType] = useState("password");
  const togglePassword = () => {
    if (passwordType == "password") {
      setPasswordType("text");
      return;
    }
    setPasswordType("password");
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <>
      <div className="container p-5">
        <form onSubmit={(e) => handleSubmit(e)}>
          <div className="mb-3">
            <label htmlFor="formGroupExampleInput" className="form-label">
              Name
            </label>
            <input
              type="text"
              className={`form-control shadow-none ${
                errors.nameErrors ? "border-danger" : ""
              }`}
              value={user.name}
              name="name"
              onChange={(e) => handleInputChange(e)}
            />
            <small className="text-danger">{errors.nameErrors}</small>
          </div>

          <div className="mb-3">
            <label htmlFor="formGroupExampleInput" className="form-label">
              Email
            </label>
            <input
              type="text"
              className={`form-control shadow-none ${
                errors.emailErrors ? "border-danger" : ""
              }`}
              value={user.email}
              name="email"
              onChange={(e) => handleInputChange(e)}
            />
            <small className="text-danger">{errors.emailErrors}</small>
          </div>

          <div className="mb-3">
            <label htmlFor="formGroupExampleInput" className="form-label">
              User Name
            </label>
            <input
              type="text"
              className={`form-control shadow-none ${
                errors.userNameErrors ? "border-danger" : ""
              }`}
              value={user.userName}
              name="userName"
              onChange={(e) => handleInputChange(e)}
            />
            <small className="text-danger">{errors.userNameErrors}</small>
          </div>

          <div className="mb-3">
            <label htmlFor="formGroupExampleInput2" className="form-label">
              password
            </label>
            <div style={{ position: "relative" }}>
              <input
                type={passwordType}
                className={`form-control shadow-none ${
                  errors.passwordErrors ? "border-danger" : ""
                }`}
                value={user.password}
                name="password"
                onChange={(e) => handleInputChange(e)}
              />
              <small className="text-danger">{errors.passwordErrors}</small>
              <i
                className="bi bi-eye-slash"
                style={{ position: "absolute", right: "10px", top: "2px" }}
                onClick={togglePassword}
              ></i>
            </div>
          </div>

          <div className="mb-3">
            <label htmlFor="formGroupExampleInput2" className="form-label">
              Repeat password
            </label>
            <div style={{ position: "relative" }}>
              <input
                type={passwordType}
                className={`form-control shadow-none ${
                  errors.repeatPasswordErrors ? "border-danger" : ""
                }`}
                value={user.repeatPassword}
                name="repeatPassword"
                onChange={(e) => handleInputChange(e)}
              />
              <small className="text-danger">
                {errors.repeatPasswordErrors}
              </small>
              <i
                className="bi bi-eye-slash"
                style={{ position: "absolute", right: "10px", top: "2px" }}
                onClick={togglePassword}
              ></i>
            </div>
          </div>

          <button type="submit" className="btn btn-primary">
            Login
          </button>
        </form>
      </div>
    </>
  );
};

export default Register;
