import { useEffect, useState } from "react";

import React from "react";

import { useSelector, useDispatch } from "react-redux";
import changeMovies from "./../../store/actions/movies";

import Spinner from "./../../Components/spinner/spinner";
import MoviCard from "./../../Components/Reuseables/MoviCard";
import "./Movies.css";

export default function Movies() {
  /* eslint eqeqeq: 0 */
  const [page, setPage] = useState(1);

  let fav = useSelector((state) => state.fav.fav);
  let spinner = useSelector((state) => state.loader.loader);
  let movies = useSelector((state) => state.movies);

  let handelPrv = () => {
    if (page > 1) {
      setPage(page - 1);
    } else {
      setPage(1);
    }
  };
  let handelNext = () => {
    setPage(page + 1);
  };

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(changeMovies(page));
  }, [page]);

  return (
    <>
      {spinner == true ? (
        <Spinner />
      ) : (
        <div className="movies">
          <div className="container pt-5 pb-5">
            <div className="row justify-content-center">
              <h2 className="text-center mb-5 col-12 head">
                We hope you have a great time
              </h2>
              {movies.map((movie, index) => {
                return (
                  <MoviCard
                    key={index}
                    movie={movie}
                    is_fav={fav.some((item) => item.id == movie.id)}
                  />
                );
              })}
            </div>
            <div className="d-flex  justify-content-center mt-3">
              <button
                className="btn btn-second me-2 p-3"
                disabled={page == 1}
                onClick={() => {
                  handelPrv();
                }}
              >
                Prev
              </button>
              <button
                className="btn btn-second "
                onClick={() => {
                  handelNext();
                }}
              >
                Next
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
