import React from "react";
import { Link } from "react-router-dom";
import "./about.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Carousel from "react-bootstrap/Carousel";
import { faShare } from "@fortawesome/free-solid-svg-icons";
import { faStar } from "@fortawesome/free-solid-svg-icons";
import { faPencil } from "@fortawesome/free-solid-svg-icons";
import { faEye } from "@fortawesome/free-solid-svg-icons";
export default function About() {
  return (
    <>
      <div className="about">
        <div className="content">
          <div className="container-fluid">
            <h1 className="text-center head">Our Team</h1>
            <div className="row rowTeam">
              <div className="col-md-3  ">
                <div className="card">
                  <div className="img-con">
                    <div className="card-img-top alaa" alt="..." />
                  </div>
                  <div className="card-body">
                    <h3 className="card-title">Alaa Ezzat</h3>
                    <h5 className="card-subTitle">Frontend Developer</h5>
                    <Link to={`/Contact`} className="btn btn-general">
                      Contact
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-md-3 ">
                <div className="card">
                  <div className="img-con">
                    <div className="card-img-top mohamed" alt="..." />
                  </div>
                  <div className="card-body">
                    <h3 className="card-title">Mohamed Elsisy</h3>
                    <h5 className="card-subTitle">Backend Developer</h5>
                    <Link to={`/Contact`} className="btn btn-general">
                      Contact
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="rates">
          <div className="container-fluid">
            <h1 className="head text-center">Number Langauge</h1>
            <div className="row rowIcons">
              <div className="col-md-3">
                <div className="rate text-center pb-4">
                  <FontAwesomeIcon className="icon" icon={faShare} />
                  <div className="iconTitle">
                    <h4>5000</h4>
                    <h4>Social Media Shares</h4>
                  </div>
                </div>
              </div>
              <div className="col-md-3">
                <div className="rate text-center pb-4">
                  <FontAwesomeIcon className="icon" icon={faStar} />
                  <div className="iconTitle">
                    <h4>1250</h4>
                    <h4>Top Rated Films</h4>
                  </div>
                </div>
              </div>
              <div className="col-md-3">
                <div className="rate text-center pb-4">
                  <FontAwesomeIcon className="icon" icon={faPencil} />
                  <div className="iconTitle">
                    <h4>300</h4>
                    <h4>Super Authors</h4>
                  </div>
                </div>
              </div>
              <div className="col-md-3">
                <div className="rate text-center pb-4">
                  <FontAwesomeIcon className="icon" icon={faEye} />
                  <div className="iconTitle">
                    <h4>3400</h4>
                    <h4>Daily Visitors</h4>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="testimonials">
          <h1 className="head text-center">Testimonials</h1>
          <Carousel>
            <Carousel.Item className="carousel-item">
              <div className="person">
                <div className="person-img one"></div>
              </div>
              <Carousel.Caption className="person-caption">
                <h3>Mohamed mahmoud</h3>
                <p>
                  Nulla vitae elit libero, a pharetra augue mollis interdum.
                </p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className="carousel-item">
              <div className="person">
                <div className="person-img two"></div>
              </div>
              <Carousel.Caption className="person-caption">
                <h3>Anna John</h3>
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item className="carousel-item">
              <div className="person">
                <div className="person-img three"></div>
              </div>
              <Carousel.Caption className="person-caption">
                <h3>Johanny Adams</h3>
                <p>
                  Praesent commodo cursus magna, vel scelerisque nisl
                  consectetur.
                </p>
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>
        </div>
      </div>
    </>
  );
}
